import React, { Ref, useEffect, useState } from 'react';
import {
    Alert,
    Title,
    Form, FormGroup,
    TextInput,
    TextArea,
    Select, SelectOption,
    Button,
    MenuToggle, MenuToggleElement,
    InputGroup,
    InputGroupItem,
} from "@patternfly/react-core";

import cockpit, { file } from 'cockpit';

const _ = cockpit.gettext;

export const STST = () => {
    const [taskName, setTaskName] = useState('');
    const [taskLogPath, setTaskLogPath] = useState('');
    const [taskResource, setTaskResource] = useState({
        node: 1, gpu_per_node: 0, cpu_per_node: 1, memory_per_node: 1
    });
    const [params, setParams] = useState('');
    const [taskType, setSelectedTaskType] = useState('task-train');
    const [isOpenTaskType, setIsOpenTaskType] = useState(false);
    const [submitStatus, setSubmitStatus] = useState<{
        variant: 'success' | 'danger' | 'warning' | 'info' | 'custom';
        title: string;
        message: string;
    } | null>(null);
    const taskTypes = [
        { value: 'task-debug', label: '调试任务' },
        { value: 'task-train', label: '训练任务' }
    ];

    const onToggleClickTaskType = () => setIsOpenTaskType(!isOpenTaskType);
    const onSelectTaskType = (_event: React.MouseEvent<Element, MouseEvent> | undefined, value: string | number | undefined) => {
        setSelectedTaskType(value as string);
        setIsOpenTaskType(false);
    };
    const toggleTaskType = (toggleRef: React.Ref<MenuToggleElement>) => (
        <MenuToggle
            ref={toggleRef}
            onClick={onToggleClickTaskType}
            isExpanded={isOpenTaskType}
        >
            {taskType}
        </MenuToggle>
    );

    const handleTaskSubmit = async () => {
        try {
            if (taskType === 'task-train') {
                // 提交训练任务
                let task_partition = 'Normal';
                let sbatch_script = `
                    #!/bin/bash
                    #SBATCH --job-name=${taskName}
                    #SBATCH --output=${taskLogPath}/${taskName}.out
                    #SBATCH --error=${taskLogPath}/${taskName}.err
                    #SBATCH --partition=${task_partition}
                    #SBATCH --nodes=${taskResource.node}
                    #SBATCH --ntasks-per-node=1
                    #SBATCH --gpus-per-task=${taskResource.gpu_per_node}
                    #SBATCH --cpus-per-task=${taskResource.cpu_per_node}
                    #SBATCH --mem=${taskResource.memory_per_node}G

                    nodes=( $( scontrol show hostnames $SLURM_JOB_NODELIST ) )
                    nodes_array=($nodes)
                    head_node=\${nodes_array[0]}
                    head_node_ip=$(srun --nodes=1 --ntasks=1 -w "$head_node" hostname --ip-address)

                    export LOGLEVEL=INFO

                    srun ${params}
                `.replace(/^\s+/gm, '');

                let user = await cockpit.user();
                let script_filename = '/tmp/slurm_' + user.id + '_' + Date.now().toString() + '.sh';
                await cockpit.file(script_filename).replace(sbatch_script);
                let res = await cockpit.spawn(['sbatch', script_filename]);

                setSubmitStatus({
                    variant: 'success',
                    title: '任务提交成功',
                    message: res
                });
            } else {
                // 提交调试任务
                let task_partition = "Debug";
                await cockpit.spawn(['salloc',
                    '--job-name', taskName,
                    '--nodes', taskResource.node.toString(),
                    '--partition', task_partition,
                    '--ntasks-per-node', '1',
                    '--gpus-per-task', taskResource.gpu_per_node.toString(),
                    '--cpus-per-task', taskResource.cpu_per_node.toString(),
                    '--mem', taskResource.memory_per_node.toString() + 'G',
                    '--no-shell'
                ]);

                let user = await cockpit.user();
                let node_info = await cockpit.spawn(['squeue', '-u', user.name, '-p', task_partition, '--format', '%N', '--noheader'])

                setSubmitStatus({
                    variant: 'success',
                    title: '任务提交成功',
                    message: [...new Set(node_info.split('\n'))].toString().replace(',', ' ')
                });
            }

            setTaskName('');
            setTaskLogPath('');
            setTaskResource(prevState => ({ ...prevState, node: 1, gpu_per_node: 0, cpu_per_node: 1, memory_per_node: 1 }));
            setParams('');
            setSelectedTaskType('task-train');
        } catch (error) {
            setSubmitStatus({
                variant: 'danger',
                title: '提交失败',
                message: `任务提交失败：${error}`
            });
        }
    };

    return (
        <div style={{ paddingLeft: '35px', paddingTop: '20px', height: '85vh', overflowY: 'auto' }}>
            <Title headingLevel='h1' style={{ fontSize: '30px', paddingBottom: '20px' }}>Slurm 任务提交工具</Title>

            <Form isWidthLimited className="pf-u-w-66">
                <FormGroup label="任务名称" isRequired fieldId="task-name">
                    <TextInput
                        id="task-name"
                        value={taskName}
                        onChange={(_event, value) => setTaskName(value)}
                        placeholder="请输入任务名称"
                        className="pf-u-w-50"
                    />
                </FormGroup>

                <FormGroup label="日志文件路径（使用绝对路径）" isRequired={taskType!='task-debug'} fieldId="task-log-path">
                    <TextInput
                        id="task-log-path"
                        value={taskLogPath}
                        onChange={(_event, value) => setTaskLogPath(value)}
                        placeholder="请输入日志文件路径"
                        className="pf-u-w-50"
                    />
                </FormGroup>

                <FormGroup label="任务类型" isRequired fieldId="task-type">
                    <Select
                        id="task-type"
                        isOpen={isOpenTaskType}
                        selected={taskType}
                        onSelect={onSelectTaskType}
                        onOpenChange={(isOpenTaskType) => setIsOpenTaskType(isOpenTaskType)}
                        toggle={toggleTaskType}
                        shouldFocusToggleOnSelect
                    >
                        {taskTypes.map((type) => (
                            <SelectOption key={type.value} value={type.value}>
                                <span className="pf-u-mr-sm">{type.label}</span>
                                <span className="pf-u-font-size-sm pf-u-color-400">({type.value})</span>
                            </SelectOption>
                        ))}
                    </Select>
                </FormGroup>

                <FormGroup label="申请资源（从左到右依次为【节点数量】【每个节点的GPU】【每个节点的CPU】【每个节点的内存(单位:G)】）" isRequired fieldId="task-resource">
                    <InputGroup>
                        <InputGroupItem>
                            <TextInput
                                id="task-resource-node"
                                value={taskResource.node}
                                onChange={(_event, value) => setTaskResource({ ...taskResource, node: Number(value) })}
                            />
                        </InputGroupItem>
                        <InputGroupItem>
                            <TextInput
                                id="task-resource-gpu"
                                value={taskResource.gpu_per_node}
                                onChange={(_event, value) => setTaskResource({ ...taskResource, gpu_per_node: Number(value) })}
                            />
                        </InputGroupItem>
                        <InputGroupItem>
                            <TextInput
                                id="task-resource-cpu"
                                value={taskResource.cpu_per_node}
                                onChange={(_event, value) => setTaskResource({ ...taskResource, cpu_per_node: Number(value) })}
                            />
                        </InputGroupItem>
                        <InputGroupItem>
                            <TextInput
                                id="task-resource-memory"
                                value={taskResource.memory_per_node}
                                onChange={(_event, value) => setTaskResource({ ...taskResource, memory_per_node: Number(value) })}
                            />
                        </InputGroupItem>
                    </InputGroup>
                </FormGroup>

                <FormGroup label="训练任务（使用绝对路径）" isRequired={taskType!='task-debug'} fieldId="task-params">
                    <TextArea
                        value={params}
                        onChange={(_event, value) => setParams(value)}
                        aria-label="训练任务及参数"
                        resizeOrientation="vertical"
                        rows={8}
                        className="pf-u-w-75"
                        style={{ fontFamily: 'monospace' }}
                    />
                </FormGroup>

                {submitStatus && (
                    <Alert
                        variant={submitStatus.variant}
                        title={submitStatus.title}
                        className="pf-u-mt-md"
                        isInline
                    >
                        {submitStatus.message}
                    </Alert>
                )}
                <div className="pf-u-mt-xl" style={{"marginBottom": "50px"}}>
                    <Button
                        variant="primary"
                        onClick={handleTaskSubmit}
                        isDisabled={!taskName || !taskType}
                    >
                        提交任务
                    </Button>
                </div>
            </Form>
        </div>
    );
};
