import React, { Ref, useEffect, useState } from 'react';
import {
    Card, CardBody, CardTitle,
    Title
} from "@patternfly/react-core";
import {
    Caption,
    Table, Thead, Th, Tbody, Tr, Td
} from "@patternfly/react-table";

import cockpit, { file } from 'cockpit';

const _ = cockpit.gettext;

interface GPUProps {
    date: string,
    time: string,
    job_id: string,
    hostname: string,
    gpu_index: string,
    gpu_usage: string,
    gpu_memory_used: string,
    gpu_memory_total: string,
}

export const SRW = () => {
    const [nodes, setNodes] = useState<any[]>([]);

    useEffect(() => {
        // 页面加载时执行
        let file: cockpit.FileHandle<string>, handle: cockpit.FileWatchHandle;
        cockpit.user().then(user => {
            let username = user.name;
            file = cockpit.file("/tmp/gpu/" + username + ".txt")
            handle = file.watch((content, tag) => {
                if (content) {
                    const lines = content.trim().split('\n');
                    const gpuPropsArray = lines.map(line => {
                        const parts = line.split(' ');
                        return {
                            date: parts[0],
                            time: parts[1],
                            job_id: parts[2],
                            hostname: parts[3],
                            gpu_index: parts[4],
                            gpu_usage: parts[5],
                            gpu_memory_used: parts[6],
                            gpu_memory_total: parts[7]
                        } as GPUProps;
                    });

                    console.log(gpuPropsArray);
                    const groupData = gpuPropsArray.reduce((acc: { [key: string]: { [key: string]: { [key: string]: GPUProps[] } } }, item) => {
                        const { job_id, hostname, gpu_index, date, time } = item;

                        const timestamp = new Date(`${date} ${time}`).getTime(); // 将日期和时间转换为时间戳

                        if (!acc[job_id]) {
                            acc[job_id] = {};
                        }
                        if (!acc[job_id][hostname]) {
                            acc[job_id][hostname] = {};
                        }
                        if (!acc[job_id][hostname][gpu_index]) {
                            acc[job_id][hostname][gpu_index] = [];
                        }

                        // 查找是否已有该GPU编号的记录，并替换为最新记录
                        const existingItem = acc[job_id][hostname][gpu_index].find(gpu => gpu.gpu_index === gpu_index);
                        if (!existingItem || new Date(`${existingItem.date} ${existingItem.time}`).getTime() < timestamp) {
                            // 替换为时间最新的记录
                            acc[job_id][hostname][gpu_index] = [item]; // 保留最新记录
                        }

                        return acc;
                    }, {});

                    setNodes(Object.entries(groupData).map(([job_id, hostData]) => ({
                        job_id,
                        hostData
                    })));
                }
            });
        })
        // 页面关闭时执行
        return () => {
            handle.remove();
            file.close();
        }
    }, []);


    return (
        <div style={{ paddingLeft: '35px', paddingRight: '35px', paddingTop: '20px', height: '85vh', overflowY: 'auto' }}>
            <Title headingLevel='h1' style={{ fontSize: '30px', paddingBottom: '20px' }}>Slurm 资源监控工具</Title>

            {
                nodes.length === 0 && <div>暂无 GPU 资源</div>
            }

            {nodes.map(({ job_id, hostData }) => (
                <div key={job_id}>
                    <Card ouiaId="SecondaryCard">
                        <CardTitle>作业 ID: {job_id}</CardTitle>
                        <CardBody>
                            {Object.entries(hostData).map(([hostname, gpuIndexData]) => (
                                <div key={hostname} style={{ width: '100%' }}>
                                    <br />
                                    <Table aria-label={hostname} style={{ width: '100%' }} >
                                        <Caption>主机：{hostname}</Caption>
                                        <Thead>
                                            <Tr style={{ lineHeight: '50px' }}>
                                                <Th style={{ width: '10%' }}>GPU 编号</Th>
                                                <Th style={{ width: '10%' }}>GPU 利用率</Th>
                                                <Th style={{ width: '20%' }}>GPU 内存已使用</Th>
                                                <Th style={{ width: '60%' }}>最后检查时间</Th>
                                            </Tr>
                                        </Thead>
                                        {Object.entries(gpuIndexData as { [gpu_index: string]: GPUProps[] }).map(([gpu_index, gpuList]) => (
                                            <Tbody key={gpu_index}>
                                                <Tr style={{ lineHeight: '30px' }} />
                                                {gpuList.map((gpu, index) => (
                                                    <Tr key={index}>
                                                        <Td>{gpu.gpu_index}</Td>
                                                        <Td>{gpu.gpu_usage}%</Td>
                                                        <Td>{gpu.gpu_memory_used} MB / {gpu.gpu_memory_total} MB</Td>
                                                        <Td>{gpu.date} {gpu.time}</Td>
                                                    </Tr>
                                                ))}
                                                <Tr />
                                            </Tbody>
                                        ))}
                                    </Table>
                                </div>
                            ))}
                        </CardBody>
                    </Card>
                </div>
            ))}

        </div>
    );
};
