import React from 'react';
import { BrowserRouter as Router, Route, Routes, Link } from 'react-router-dom';
import { SRW } from './srw';
import { STST } from './stst';
import {
    Masthead, MastheadBrand, MastheadMain, MastheadToggle, MastheadContent,
    ToolbarItem,
    Nav, NavItem, NavList
} from "@patternfly/react-core";
import { BarsIcon } from '@patternfly/react-icons';

interface NavOnSelectProps {
    groupId: number | string;
    itemId: number | string;
    to: string;
}
export const Application = () => {
    const [activeItem, setActiveItem] = React.useState(0);
    const onNavSelect = (_event: React.FormEvent<HTMLInputElement>, selectedItem: NavOnSelectProps) => {
        typeof selectedItem.itemId === 'number' && setActiveItem(selectedItem.itemId);
    };

    return (
        <Router>
            <Masthead>
                <MastheadToggle>
                    <button aria-label="Toggle navigation" style={{ background: 'none', border: 'none', color: '#fff' }}>
                        <BarsIcon />
                    </button>
                </MastheadToggle>
                <MastheadMain>
                    <MastheadBrand>Slurm 工具</MastheadBrand>
                </MastheadMain>
                <MastheadContent>
                    <ToolbarItem isOverflowContainer>
                        <Nav
                            id="page-layout-horizontal-nav-horizontal-nav"
                            onSelect={onNavSelect}
                            aria-label="Global"
                            variant="horizontal"
                        >
                            <NavList>
                                <NavItem itemId={0} isActive={activeItem === 0}>
                                    <Link to="/">作业提交</Link>
                                </NavItem>
                                <NavItem itemId={1} isActive={activeItem === 1}>
                                    <Link to="/srw">资源监控</Link>
                                </NavItem>
                            </NavList>
                        </Nav>
                    </ToolbarItem>
                </MastheadContent>
            </Masthead>
            <Routes>
                <Route path='/*' element={<STST />} />
                <Route path='/srw' element={<SRW />} />
            </Routes>
        </Router>
    );
};
